.about-us {
  @include page-grid;

  &__title {
    grid-column: 1 /-1;
    margin-bottom: 32px;

    @include on-tablet {
      grid-column: 1 / 4;
    }

    @include on-desktop {
      grid-column: 1 /7;
    }
  }
  &__content {
    grid-column: 1 /-1;

    @include on-tablet {
      grid-column: 4 / 7;
    }

    @include on-desktop {
      grid-column: 7 /12;
    }
  }

  &__text {
    margin: 0;
  }
}
